'use client'

import { useState, useEffect } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { ArrowUpRight } from 'lucide-react'
import { useRef } from 'react'

// Temporary static data - will be replaced with API data
const staticProducts = [
  {
    id: '1',
    name: 'Titan Raga Viva Silver Dial',
    price: 9406.25,
    category: 'women',
    image: '/images/products/watch1.jpg',
  },
  {
    id: '2',
    name: 'Titan Raga Viva White Dial',
    price: 11287.50,
    category: 'women',
    image: '/images/products/watch2.jpg',
  },
  {
    id: '3',
    name: 'Titan Raga Chic Quartz',
    price: 19565.00,
    category: 'men',
    image: '/images/products/watch3.jpg',
  },
  {
    id: '4',
    name: 'Titan Raga Viva Champagne',
    price: 9782.50,
    category: 'men',
    image: '/images/products/watch4.jpg',
  }
]

export default function ProductSection() {
  const [products, setProducts] = useState(staticProducts)
  const [loading, setLoading] = useState(false)
  const scrollRef = useRef<HTMLDivElement>(null)

  // Later uncomment this to use API

  useEffect(() => {
    fetchProducts()
  }, [])

 const fetchProducts = async () => {
  try {
    setLoading(true)
    const response = await fetch('http://localhost:8000/api/products?is_featured=true&limit=4')
    if (response.ok) {
      const data = await response.json()

      // Fix image paths
      const productsWithImages = data.map((product: any) => ({
        ...product,
        image: product.images && product.images.length > 0
          ? product.images[0]
          : '/images/products/placeholder.jpg'  // Fallback image
      }))

      setProducts(productsWithImages)
    }
  } catch (error) {
    console.error('Error fetching products:', error)
    setProducts(staticProducts)
  } finally {
    setLoading(false)
  }
}


  if (loading) {
    return (
      <section className="py-16 md:py-24 bg-black">
        <div className="container-custom text-center">
          <p className="text-white">Loading products...</p>
        </div>
      </section>
    )
  }

  return (
    <section className="py-16 md:py-24 bg-black relative z-10">
      <div className="container-custom">
        {/* Section Header */}
        <div className="text-center mb-12">
          <p className="text-white/80 mb-2 text-base md:text-lg">
            For every moment, a timeless companion.
          </p>
          <h2 className="text-4xl md:text-6xl font-light mb-4 text-white">
            Introducing Timora Masterpieces
          </h2>
        </div>

        {/* Desktop Product Grid */}
        <div className="hidden md:grid md:grid-cols-4 gap-6">
          {products.map((product) => (
            <div key={product.id} className="group relative">
              <Link href={`/products/${product.id}`}>
                <div className="relative aspect-square bg-gray-900 rounded-lg overflow-hidden">
                  <Image
                    src={product.image}
                    alt={product.name}
                    fill
                    className="object-cover group-hover:scale-105 transition-transform duration-500"
                    sizes="25vw"
                  />

                  <div className="absolute bottom-2 left-2 bg-black/90 text-white px-3 py-1 rounded text-xs font-light tracking-wider uppercase">
                    {product.category}
                  </div>

                  <div className="absolute top-2 right-2 bg-white/90 backdrop-blur-sm rounded-full p-2 opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                    <ArrowUpRight className="w-4 h-4 text-black" />
                  </div>
                </div>

                <div className="mt-4">
                  <h3 className="text-lg font-light text-white line-clamp-1">
                    {product.name}
                  </h3>
                  <p className="text-lg font-light text-white mt-1">
                    ৳{product.price.toFixed(2)} BDT
                  </p>
                </div>
              </Link>
            </div>
          ))}
        </div>

        {/* Mobile Product Carousel */}
        <div className="md:hidden">
          <div
            ref={scrollRef}
            className="flex gap-4 overflow-x-auto snap-x snap-mandatory scrollbar-hide"
            style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}
          >
            {products.map((product) => (
              <div key={product.id} className="flex-shrink-0 w-[85vw] snap-center">
                <Link href={`/products/${product.id}`}>
                  <div className="relative aspect-square bg-gray-900 rounded-lg overflow-hidden">
                    <Image
                      src={product.image}
                      alt={product.name}
                      fill
                      className="object-cover"
                      sizes="85vw"
                    />

                    <div className="absolute bottom-3 left-3 bg-black/90 text-white px-3 py-1 rounded text-sm font-light tracking-wider uppercase">
                      {product.category}
                    </div>

                    <div className="absolute top-3 right-3 bg-white/90 backdrop-blur-sm rounded-full p-2">
                      <ArrowUpRight className="w-4 h-4 text-black" />
                    </div>
                  </div>

                  <div className="mt-4">
                    <h3 className="text-xl font-light text-white">
                      {product.name}
                    </h3>
                    <p className="text-lg font-light text-white mt-1">
                      ৳{product.price.toFixed(2)} BDT
                    </p>
                  </div>
                </Link>
              </div>
            ))}
          </div>

          <div className="flex justify-center gap-2 mt-6">
            {products.map((_, index) => (
              <div
                key={index}
                className="w-2 h-2 rounded-full bg-gray-600"
              />
            ))}
          </div>
        </div>

        {/* Explore All Button */}
        <div className="text-center mt-12">
          <Link
            href="/products"
            className="inline-flex items-center gap-2 px-8 py-3 bg-white text-black rounded-lg hover:bg-gray-200 transition-colors"
          >
            Explore All Products
            <ArrowUpRight className="w-4 h-4" />
          </Link>
        </div>
      </div>
    </section>
  )
}