'use client'

import Image from 'next/image'
import Link from 'next/link'
import { useRef, useEffect, useState } from 'react'
import { ChevronLeft, ChevronRight } from 'lucide-react'

const brands = [
  { id: 1, name: 'Seiko', slug: 'seiko', image: '/images/brands/seiko.jpg' },
  { id: 2, name: 'Casio', slug: 'casio', image: '/images/brands/casio.jpg' },
  { id: 3, name: 'Citizen', slug: 'citizen', image: '/images/brands/citizen.jpg' },
  { id: 4, name: 'Naviforce', slug: 'naviforce', image: '/images/brands/naviforce.jpg' },
  { id: 5, name: 'Curren', slug: 'curren', image: '/images/brands/curren.jpg' },
  { id: 6, name: 'Hilfiger', slug: 'hilfiger', image: '/images/brands/hilfiger.jpg' },
]

export default function BrandSection() {
  const scrollRef = useRef<HTMLDivElement>(null)
  const [canScrollLeft, setCanScrollLeft] = useState(false)
  const [canScrollRight, setCanScrollRight] = useState(true)

  const checkScroll = () => {
    if (scrollRef.current) {
      const { scrollLeft, scrollWidth, clientWidth } = scrollRef.current
      setCanScrollLeft(scrollLeft > 0)
      setCanScrollRight(scrollLeft < scrollWidth - clientWidth - 10)
    }
  }

  useEffect(() => {
    const container = scrollRef.current
    if (container) {
      container.addEventListener('scroll', checkScroll)
      checkScroll()
    }
    return () => container?.removeEventListener('scroll', checkScroll)
  }, [])

  const scroll = (direction: 'left' | 'right') => {
    if (scrollRef.current) {
      const scrollAmount = 300
      const currentScroll = scrollRef.current.scrollLeft
      
      scrollRef.current.scrollTo({
        left: direction === 'left' 
          ? currentScroll - scrollAmount 
          : currentScroll + scrollAmount,
        behavior: 'smooth'
      })
    }
  }

  return (
    <section className="py-16 md:py-24 bg-black relative z-10">
      <div className="container-custom">
        {/* Section Header */}
        <div className="text-center mb-12">
          <p className="text-white/80 mb-2 text-base md:text-lg">
            Explore the finest timepieces by your favorite brands.
          </p>
          <h2 className="text-4xl md:text-6xl font-light text-white">
            Curated Collections by Brand
          </h2>
        </div>

        {/* Desktop Brand Carousel */}
        <div className="relative hidden md:block">
          {/* Navigation Buttons */}
          {canScrollLeft && (
            <button
              onClick={() => scroll('left')}
              className="absolute left-0 top-1/2 -translate-y-1/2 z-10 bg-black/80 backdrop-blur-sm text-white p-3 rounded-full hover:bg-black/90 transition-colors"
            >
              <ChevronLeft className="w-6 h-6" />
            </button>
          )}
          
          {canScrollRight && (
            <button
              onClick={() => scroll('right')}
              className="absolute right-0 top-1/2 -translate-y-1/2 z-10 bg-black/80 backdrop-blur-sm text-white p-3 rounded-full hover:bg-black/90 transition-colors"
            >
              <ChevronRight className="w-6 h-6" />
            </button>
          )}

          {/* Brand Grid */}
          <div 
            ref={scrollRef}
            className="flex gap-6 overflow-x-auto scrollbar-hide"
            style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}
          >
            {brands.map((brand) => (
              <Link
                key={brand.id}
                href={`/brands/${brand.slug}`}
                className="flex-shrink-0 w-[250px] group"
              >
                <div className="relative aspect-[3/4] rounded-lg overflow-hidden bg-gray-900">
                  <Image
                    src={brand.image}
                    alt={brand.name}
                    fill
                    className="object-cover group-hover:scale-105 transition-transform duration-500"
                    sizes="250px"
                  />
                  {/* Brand Name Overlay */}
                  <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black via-black/90 to-transparent p-6">
                    <h3 className="text-white text-2xl md:text-3xl font-light tracking-wider text-center">
                      {brand.name}
                    </h3>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        </div>

        {/* Mobile Brand Carousel */}
        <div className="md:hidden">
          <div className="flex gap-4 overflow-x-auto snap-x snap-mandatory scrollbar-hide">
            {brands.map((brand) => (
              <Link
                key={brand.id}
                href={`/brands/${brand.slug}`}
                className="flex-shrink-0 w-[75vw] snap-center"
              >
                <div className="relative aspect-[3/4] rounded-lg overflow-hidden bg-gray-900">
                  <Image
                    src={brand.image}
                    alt={brand.name}
                    fill
                    className="object-cover"
                    sizes="75vw"
                  />
                  {/* Brand Name Overlay */}
                  <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black via-black/90 to-transparent p-6">
                    <h3 className="text-white text-xl font-light tracking-wider text-center">
                      {brand.name}
                    </h3>
                  </div>
                </div>
              </Link>
            ))}
          </div>

          {/* Mobile Indicators */}
          <div className="flex justify-center gap-2 mt-6">
            {brands.map((_, index) => (
              <div
                key={index}
                className="w-2 h-2 rounded-full bg-gray-600"
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}
